import React, { Component } from 'react'
import { connect } from 'react-redux'
import './style.scss'

class Video extends Component {

    constructor(props) {
        super(props)
        this.state = {
            videoItemDisplay: "none",
            currentVideoSrc: ""
        }
    }

    componentDidMount() {

    }

    componentDidUpdate() {
    }


    selectVideo = (videoSrc) => {
        this.setState({
            currentVideoSrc: videoSrc,
            videoItemDisplay: "block"
        })
        this.forceUpdate()
    }

    closePlayBox = () => {
        this.setState({
            videoItemDisplay: "none"
        })
        this.forceUpdate()
    }

    render() {
        let {allVideos} = this.props
        return (
            <div className="videos-wrapper">
                {allVideos.map((video, index) => {
                    return <div className="video-item" key={index}>
                        <span className="play-icon" onClick={() => this.selectVideo(video)}>
                            <i></i>
                        </span>
                        <video muted >
                            <source src={video} />
                        </video>
                    </div>
                })}
                <div className="play-box" style={{ display: this.state.videoItemDisplay }}>
                    <i className="close-icon" onClick={() => this.closePlayBox()}></i>
                    <video ref="play-video" muted src={this.state.currentVideoSrc} controls="controls"></video>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        allVideos: state.allVideos
    }
}

export default connect(mapStateToProps)(Video)